/* Ensure the body can scroll naturally */
html, body {
    margin: 0;
    padding: 0;
    height: 100%;
    overflow-y: auto;  /* Let the browser handle vertical scrolling */
}

/* Home page setup */
.home-page {
    min-height: auto; /* Ensure the home page takes up full viewport height */
    overflow-y: visible;
}

/* Hero Section */
.hero-section {
    height: 70vh; /* Ensure the hero section takes up 70% of the viewport height */
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%; /* Ensure it takes the full width */
    z-index: 1; /* Ensure it's above other content */
}

/* Overlay for text */
.hero-overlay {
    background-color: rgba(0, 0, 0, 0.6); /* Add overlay for better text readability */
    padding: 40px;
    border-radius: 8px;
}

.hero-overlay h1 {
    color: white;
    font-size: 4rem; /* Adjust size for better visibility */
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 2px;
    text-align: center;
    margin: 0;
}

/* Grid Section */
.grid-section {
    display: grid;
    grid-template-columns: repeat(3, 1fr);  /* 3 items per row */
    gap: 20px;  /* Add space between grid items */
    padding: 40px;
    max-width: 1200px;
    margin: 0 auto;
}

.grid-item {
    text-align: center;
}

.grid-item img {
    width: 100%;
    height: auto;
    border-radius: 10px;
    object-fit: cover;
}

.grid-item h2 {
    margin-top: 10px;
    font-size: 1.5rem;
    font-weight: bold;
    color: #333;
}
